import React, {Component} from 'react';
import './Contact.scss';
import { Button } from '../Button/Button';
import axios from 'axios';

export default class ContactForm extends Component{

state={
    firstname:'',
    lastname:'',
    email:'',
    phone:'',
    typeOfSite:'',
    message:'',
    sent:false
}

//starts handle inputs

handleFirstname=(e)=> {
    this.setState({
        firstname:e.target.value
    })
}
handleLasttname=(e)=> {
    this.setState({
        lastname:e.target.value
    })
}
handleEmail=(e)=> {
    this.setState({
        email:e.target.value
    })
}
handlePhone=(e)=> {
    this.setState({
        phone:e.target.value
    })
}
handleType=(e)=> {
    this.setState({
        typeOfSite:e.target.value
    })
}
handleMessage=(e)=> {
    this.setState({
        message:e.target.value
    })
}

//end handle inputs

formSubmit=(e)=>{
    e.preventDefault();

    let data = {
        firstname:this.state.firstname,
        lastname:this.state.lastname,
        email:this.state.email,
        phone:this.state.phone,
        typeOfSite:this.state.typeOfSite,
        message:this.state.message
    }
    axios.post('/api/forma', data)
    .then(res=>{
        this.setState({
            sent:true
        },this.resetForm())
    }).catch(()=>{
        console.log('message not send');
    })
}

//reset system
resetForm=()=>{
    this.setState({
        firstname:'',
        lastname:'',
        email:'',
        phone:'',
        typeOfSite:'',
        message:'',
    })
    setTimeout(()=>{
        this.setState({
            sent:false,
        })
    },3000)
}



render(){
    return(
    <>
        <section className="contactPage" id="contact">
            <h1 className="h1-contact">Contact Me</h1>    
            <div id="contact-container">
                <div className="contact-info">                    
                    <h4 className="h4-contact">Contact Information</h4>
                    <p className="p-contact">Fill up the form and i will get back to you within 24 hours !</p>
                    <div className="icon-text">
                        <i className="fas fa-phone-alt"></i>
                        <span>+40 760 114 914</span>
                    </div>
                    <div className="icon-text">
                        <i className="fas fa-envelope"></i>
                        <span>stefan.mihai.ghe@gmail.com</span>
                    </div>
                    <div className="icon-text">
                        <i className="fas fa-map-marker-alt"></i>
                        <span>Romania, Bucharest</span>
                    </div>
                    <div className="social-media">
                        <a href="https://ro-ro.facebook.com/" className="icon-circle">
                        <i className="fab fa-facebook"></i>
                        </a>
                
                        <a href="https://ro-ro.facebook.com/" className="icon-circle">
                        <i className="fab fa-instagram"></i>
                        </a>
           
                        <a href="https://ro-ro.facebook.com/" className="icon-circle">
                        <i className="fab fa-twitter"></i>
                        </a>
                   
                        <a href="https://ro-ro.facebook.com/" className="icon-circle">
                        <i className="fab fa-youtube"></i>
                        </a>
                    </div>    
                </div>
                <form className="form-contact" onSubmit={this.formSubmit}>
                    <div className="col">
                        <div className="form-group">
                            <label className="label-contact">First Name</label>
                            <input type="text" value={this.state.firstname} onChange={this.handleFirstname} required/>
                        </div>
                        <div className="form-group">
                            <label className="label-contact">Last Name</label>
                            <input type="text" value={this.state.lastname} onChange={this.handleLasttname} required/>
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label className="label-contact">E-Mail</label>
                            <input type="email" value={this.state.email} onChange={this.handleEmail} required/>
                        </div>
                        <div className="form-group">
                            <label className="label-contact">Phone #</label>
                            <input type="tel" value={this.state.phone.toString()} onChange={this.handlePhone}/>
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group solo">
                            <label className="label-contact">What kind of site do you need?</label>
                            <div className="radio-buttons">
                                <div className="radio-button">
                                    <input type="radio"
                                        id="radiopersonal" 
                                        name="type" 
                                        value="Personal"
                                        onChange={this.handleType}
                                        required
                                        /><label className="label-contact" htmlFor="radiopersonal">Personal</label>
                                </div>
                                <div className="radio-button">
                                    <input type="radio" 
                                        id="radioecommerce" 
                                        name="type" 
                                        value="E-Commerce"
                                        onChange={this.handleType}
                                        required
                                        /><label className="label-contact" htmlFor="radioecommerce">E-Commerce</label>
                                </div>
                                <div className="radio-button">
                                    <input type="radio" 
                                        id="radiolandingpage" 
                                        name="type" 
                                        value="LandingPage"
                                        onChange={this.handleType}
                                        required
                                        /><label className="label-contact" htmlFor="radiolandingpage" >Landing Page</label>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group solo">
                            <label className="label-contact">Message</label>
                            <textarea value={this.state.message} onChange={this.handleMessage} required></textarea>
                        </div>
                    </div>
                    <div className="col-2" id="btn-mess">
                        <div className={this.state.sent ? "confirmation-messageAppear":"confirmation-message"}>
                            <label className="confirmation-text">Message Send!</label>
                            <i className="fas fa-check"></i>
                        </div>
                        <div className="contact-submit">
                            <Button type="submit" className='btns' buttonStyle='btn--primary' > 
                                Send Message!
                            </Button>
                        </div>
                    </div>
                </form>
            </div>
        </section>
    </>
    )
}
}
